body {
  margin: 0;
  font-family: "GothamPro", sans-serif;
  background-color: #fff;
  max-width: 1920px;
  min-width: 320px;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1230px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.btn {
  display: inline-block;
  border-radius: 30px;
  padding: 15px 60px;
  font-weight: 700;
  font-size: 12px;
  line-height: 2.222;
  text-transform: uppercase;
  color: #fff;
  transition: all 0.4s linear;
}

.btn--gray {
  background-color: #6a6a6a;
}

.btn--gray:hover {
  color: #6a6a6a;
  background-color: #fff;
}

.btn--yellow {
  background-color: #f6b442;
}

.btn--yellow:hover {
  color: #f6b442;
  background-color: #fff;
}

.section__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.section__title {
  margin-bottom: 10px;
  font-weight: 900;
  font-size: 32px;
  line-height: 1.2;
  text-align: center;
  color: #363636;
}

.section__title--yellow {
  color: #f6b442;
}

.section__sub-title {
  font-weight: 400;
  font-size: 17px;
  color: #a3a3a3;
}

#jGrowl {
  z-index: 99999;
  font-size: 18px;
}

.mt-10 {
  margin-top: 10px;
}

.mt-20 {
  margin-top: 20px;
}

.mt-40 {
  margin-top: 40px;
}

.mb-10 {
  margin-bottom: 10px;
}

.breadcrumbs {
  margin-top: 20px;
}

.breadcrumbs__block {
  display: flex;
  justify-content: center;
}

.breadcrumbs__link-home {
  font-size: 17px;
  color: #fff;
}

.breadcrumbs__slash {
  font-size: 17px;
  color: #fff;
}

.breadcrumbs__link--current {
  font-size: 17px;
  color: #a3a3a3;
}

.yellow-text {
  color: #f6b442;
}

@media (max-width: 1100px) {
  .breadcrumb-wrapper {
    text-align: center;
  }
}

@media (max-width: 500px) {
  .section__title {
    font-size: 25px;
  }
}

.header {
  position: sticky;
  top: 0;
  z-index: 150;
  padding: 20px 0 0;
  background-color: #f6b442;
}

.header__top {
  display: flex;
  justify-content: space-between;
}

.header__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 420px;
}

.header-contact {
  display: flex;
}

.header-contact__block {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header-contact__block:not(:last-child) {
  margin-bottom: 10px;
}

.header-contact__icon {
  margin-right: 5px;
}

.header-contact__link {
  font-weight: 500;
  font-style: normal;
  color: #fff;
  transition: all 0.5s linear;
}

.header-contact__link:hover {
  color: #363636;
}

.header__navigation {
  margin-top: 25px;
}

.eya {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 15px;
}

.eya__link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.eya__mob {
  display: none;
}

.eya__title {
  margin-top: 10px;
  max-width: 130px;
  font-weight: 600;
  font-size: 15px;
  line-height: 1.267;
  text-align: center;
  color: #fff;
}

.logo {
  position: relative;
}

.logo__sup-title {
  font-weight: 700;
  font-size: 11px;
  line-height: 1.5;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}

.logo__title {
  font-weight: 900;
  font-style: normal;
  font-size: 22px;
  line-height: 1.2;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}

.logo__link {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.nav__list {
  display: flex;
  justify-content: center;
  margin-bottom: 0;
}

.nav__list--dropdown {
  position: absolute;
  border-radius: 20px;
  background-color: #fff;
  left: 0;
  top: 40px;
  padding: 20px;
  width: 100%;
  min-width: 300px;
  display: block;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.2);
  transition: all .5s linear;
  opacity: 0;
  visibility: hidden;
}

.nav__item {
  position: relative;
  padding-bottom: 16px;
  transition: all .5s ease-in-out;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__item:hover .nav__list--dropdown {
  visibility: visible;
  opacity: 1;
  z-index: 10;
  transition: .3s;
}

.nav__item--current .nav__link {
  color: #fff;
}

.nav__link {
  font-weight: 700;
  font-size: 19px;
  line-height: 0.667;
  color: #363636;
  transition: all 0.5s linear;
}

.nav__link:hover {
  color: #fff;
}

.nav__link--drop::after {
  content: "";
  width: 10px;
  height: 10px;
  background-image: url(../img/download.svg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: inline-block;
  margin-left: 5px;
  transition: transform .5s ease-in-out;
}

.dropdown-list__item:not(:last-child) {
  margin-bottom: 10px;
}

.dropdown-list__link {
  font-size: 17px;
  color: #6a6a6a;
  transition: all .5s ease-in-out;
}

.dropdown-list__link:hover {
  color: #000;
}

@media (min-width: 1001px) and (max-width: 1290px) {
  .header__navigation {
    overflow: hidden;
  }
}

@media (max-width: 1230px) {
  .nav__link {
    font-size: 17px;
  }
}

@media (max-width: 1120px) {
  .nav__link {
    font-size: 15px;
  }
}

@media (min-width: 1000px) {
  .burger {
    display: none;
  }
}

@media (max-width: 1000px) {
  .burger {
    display: block;
  }
  .eya__mob {
    display: block;
  }
  .nav {
    display: none;
  }
  .nav__list {
    flex-direction: column;
    margin: 0;
    width: 100%;
  }
  .nav__list--dropdown {
    display: none;
    position: relative;
    opacity: 1;
    visibility: inherit;
    box-shadow: none;
    padding-top: 0;
    padding-bottom: 0;
    transition: none;
  }
  .nav__item {
    padding: 10px 0;
    width: 100%;
    border-bottom: 1px solid #000;
  }
  .nav__item:not(:last-child) {
    margin-right: 0;
  }
  .nav__item--current .nav__link {
    color: #f6b442;
  }
  .nav__item:hover .nav__list--dropdown {
    transition: none;
  }
  .nav__link {
    display: block;
    width: 100%;
    text-align: center;
  }
  .nav__link:hover {
    color: #f6b442;
  }
  .nav__link--current {
    color: #f6b442;
  }
  .dropdown-list {
    margin-top: 10px;
    top: 100%;
  }
  .dropdown-list__item {
    width: 100%;
    text-align: center;
  }
  .dropdown-list__link {
    font-size: 14px;
  }
  .dropdown-list__link:first-child {
    margin-top: 10px;
  }
  .header {
    padding: 10px;
  }
  .header__navigation {
    position: absolute;
    left: 0;
    top: 78px;
    margin-top: 0;
    width: 100%;
    z-index: 200;
    background-color: #fff;
  }
  .header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header__contact {
    display: none;
  }
}

@media (max-width: 600px) {
  .eya__mob {
    margin-right: 10px;
  }
}

@media (max-width: 560px) {
  .header__logo {
    width: 330px;
  }
  .header__logo .logo__sup-title {
    font-size: 9px;
  }
  .header__logo .logo__title {
    font-size: 15px;
  }
  .header__navigation {
    top: 64px;
  }
  .header .nav__link {
    font-size: 14px;
  }
}

@media (max-width: 500px) {
  .header {
    padding: 10px 0;
  }
  .header__logo {
    width: 260px;
  }
  .header__logo .logo__sup-title {
    font-size: 7px;
  }
  .header__logo .logo__title {
    font-size: 13px;
  }
  .header__navigation {
    top: 55px;
  }
}

@media (max-width: 400px) {
  .header {
    padding: 10px 0;
  }
  .header__logo {
    width: 200px;
  }
  .header__logo .logo__sup-title {
    font-size: 5px;
  }
  .header__logo .logo__title {
    font-size: 10px;
  }
  .header__navigation {
    top: 47px;
  }
}

.hero {
  clip-path: polygon(100% 0, 100% 86%, 50% 100%, 0 86%, 0 0);
}

.hero__slider {
  height: 83.9vh;
}

.hero__slide {
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero__slide::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.hero__pagination {
  bottom: 55px !important;
}

.hero__pagination .swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  background: #fff;
  opacity: 1;
}

.hero__pagination .swiper-pagination-bullet-active {
  background: #f6b442;
}

.hero__container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  z-index: 6;
}

.hero__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.hero__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.hero__sup-title {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}

.hero__title {
  margin: 10px 0;
  font-weight: 900;
  font-size: 70px;
  line-height: 0.929;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}

.hero__sub-title {
  max-width: 880px;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.2;
  text-align: center;
  color: #fff;
}

.hero__button {
  margin-top: 30px;
}

.hero__btn:not(:last-child) {
  margin-right: 20px;
}

@media (max-width: 800px) {
  .hero__title {
    font-size: 35px;
  }
}

@media (max-width: 600px) {
  .hero__button {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .hero__btn:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

@media (max-width: 500px) {
  .hero__title {
    font-size: 25px;
  }
  .hero__sup-title {
    font-size: 15px;
  }
  .hero__sub-title {
    font-size: 15px;
  }
  .hero__btn {
    width: 80%;
    text-align: center;
    padding: 7px 30px;
  }
}

.specialty {
  padding: 40px 0;
}

.specialty__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px;
}

.specialty__item {
  margin-bottom: 25px;
  border-radius: 10px;
  width: 32%;
  background-color: #e9e9e9;
  transition: all 0.5s linear;
  cursor: pointer;
}

.specialty__item:hover {
  box-shadow: 0 0 22px 0 rgba(0, 0, 0, 0.35);
}

.specialty__img {
  overflow: hidden;
}

.specialty__img img {
  border-radius: 10px 10px 0 0;
  width: 100%;
  min-height: 250px;
  max-height: 250px;
  object-fit: cover;
}

.specialty__info {
  padding: 0 30px 40px 30px;
}

.specialty__title {
  display: flex;
  align-items: flex-end;
  margin: 0;
  min-height: 70px;
  font-weight: 900;
  font-size: 25px;
  line-height: 1.4;
  color: #363636;
}

.specialty__block {
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

